import React, { useState } from "react"
import { Tabs, Button, Drawer, Input, Upload } from "antd"
import { UploadOutlined } from "@ant-design/icons-svg"
import { withRouter } from 'react-router-dom'

import { Section } from "../../components"
import { Cities, People, News, AdAdmin } from '../../container'
const Manager = props => {
	const { TabPane } = Tabs
	
	return (
		<Section title='' color='orange' type='description'>
			<Tabs defaultActiveKey='1' tabPosition='left' style={{ height: "100%" }}>
				<TabPane tab={""} key='1'>
					<Cities />
				</TabPane>
				<TabPane tab={""} key='2'>
					<AdAdmin />
				</TabPane>
				<TabPane tab={""} key='3'>
					<People />
				</TabPane>
				<TabPane tab={""} key='4'>
					<News />
				</TabPane>
				<TabPane tab={""} key='5'>
					
				</TabPane>
			</Tabs>
		</Section>
	)
}

export default withRouter(Manager)
